html {
  font-family: 'Raleway', sans-serif;
}

body {
  margin: 0;
  padding: 0;
  height: 100%;
  overflow-x: hidden;
  font-size: 10px;

  background: rgb(246, 245, 245);
  color: #333;
}

.content {
  min-height: calc(100vh - 2rem);
  -webkit-touch-callout: none; /* iOS Safari */
  -webkit-user-select: none;   /* Chrome/Safari/Opera */
  -moz-user-select: none;      /* Firefox */
  -ms-user-select: none;       /* Internet Explorer/Edge */
  user-select: none;
}

header {
  display: flex;
  justify-content: space-between;
  align-items: center;

  background: linear-gradient(#838383, rgb(246, 245, 245));
}

h1 {
  margin: 0;
  padding: 2rem 0.5rem 2rem 2rem;
  font-size: 3rem;
}

header div {
  display: grid;
  justify-items: end;
  gap: 1rem;
  padding-right: 1rem;
  font-size: 1rem;
}

.date span{
  float: right;
}

.language{
  cursor: pointer;
  border-bottom: solid 1px rgba(253, 244, 229, 0);
  font-size: 1.2rem;
}

.language:hover, .language:focus {
  color: seagreen;
  border-bottom: solid 1px seagreen ;
}

.currencies-block {
  display: flex;
  align-content: space-between;
  align-items: stretch;
  flex-wrap: wrap;

  margin-bottom: 2rem;
  padding: 0 3rem 0 1.3rem;

  background-color: #333;
  color: rgb(246, 245, 245);
}

.currencies-block div {
  width: 3rem;
  padding: 0.7rem;
  cursor: pointer;
  text-align: center;
  font-size: 1rem;
}

.currencies-block div:hover, .currencies-block div:focus{
  background-color: seagreen;
}

.tooltip {
  position: relative;
  display: inline-block;
}

.tooltiptext {
  font-size: 0.8rem;
}

.tooltip .tooltiptext {
  position: absolute;
  padding: 0.2rem;
  max-height: 3rem;
  width: 8rem;
  bottom: 100%;
  left: 0;
  background-color: seagreen;
  color: rgb(246, 245, 245);
  text-align: center;

  visibility: hidden;
  opacity: 0;
  transition: opacity 0.5s;
}

.tooltip:hover .tooltiptext, .tooltip:focus .tooltiptext {
  visibility: visible;
  opacity: 1;
}

.inputs {
  display: flex;
  align-items: baseline;
  flex-wrap: wrap;
  gap: 1rem;
  padding: 0 2rem 2rem 2rem;
}

.input-item form label {
  font-size: 0.8rem;
}

.input-item label{
  display: inline-block;
  padding: 0.2rem;
  font-size: 1rem;
  color: #838383;
}


.delete {
  margin-left: 4rem;
  padding: 0.1rem 1rem;
  position: absolute;
  font-size: 0.8rem;
  cursor: pointer;

  opacity: 0;
  transition: opacity 0.5s;
}

.input-item:hover .delete, .delete:focus {
  visibility: visible;
  opacity: 1;
}

.delete:hover, .delete:focus {
  color: red;
}

input {
  display: block;
  margin-bottom: 1rem;
  max-width: 10rem;
  height: 2rem;

  outline:none;
  border: none;
  background: transparent;


  font-family: 'Raleway', sans-serif;
  font-size: 1.5rem;
}

footer {
  padding: 0 2rem;
}

footer a:hover, footer a:focus{
  color: seagreen;
}

footer a:visited{
  color: #838383;
}


@media screen and (max-width: 900px) {

  h1{
    font-size: 2.5rem;
  }

  .inputs {
    padding: 0 1rem 1rem 2rem;
    gap: 0.5rem;
  }
}

@media screen and (max-width: 450px) {

  h1{
    padding: 1rem;
    font-size: 2rem;
  }

  .language{
    font-size: 1rem;
  }

  header div {
    display: grid;
    justify-items: end;
    gap: 0.7rem;
    padding-right:0.5rem;
    font-size: 0.7rem;
  }
}



